@font-face {
  font-family: paint-number;
  src: url("../../public/fonts/edo.ttf");
}
@font-face {
  font-family: paint-text;
  src: url("../../public/fonts/PAINP___.TTF");
}
.fullDiv {
  height: 100%;
  width: 100%;
}

.card2 {
  margin: 1em;
  border: 3px dashed orange;
}
.card2 p, .card2 h4 {
  padding: 0;
  margin: 0;
}

div.fakecard {
  height: calc(100 * var(--magic, 0.1em));
  display: inline-block;
  border-radius: calc(5 * var(--magic));
  font-size: calc(10 * var(--magic, 0.1px));
  box-shadow: inset 0 0 1em 0.2em #000;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
}
div.fakecard p {
  margin: 0;
  padding: 0;
  font-family: "paint-number", monospace;
  color: rgba(255, 0, 0, 0.2666666667);
  text-align: center;
}

div.card-holder[owner="1"] div.fakecard p {
  display: none;
  color: transparent;
}

div.card {
  background-color: #bda583;
  text-align: center;
  display: block;
  font-size: calc(10 * var(--magic, 0.1px));
  height: calc(100 * var(--magic, 0.1em) - 0.5em);
  display: inline-block;
  padding: 0.25em;
  cursor: grab;
  color: #292418;
  border-radius: calc(3 * var(--magic));
  --card-rem: calc(10*var(--magic,0.1px));
}
div.card .card-title {
  display: flex;
  justify-content: center;
  border-bottom: 5px solid #292418;
  height: 16%;
  font-family: "paint-number", monospace;
}
div.card .imgbox {
  position: relative;
  border-bottom: 5px solid #292418;
  height: 40%;
}
div.card .imgbox .fullDiv > svg {
  height: 100%;
  width: 100%;
}
div.card .imgbox .cost {
  position: absolute;
  top: 0;
  right: 0.25em;
  display: flex;
  gap: 0.1em;
}
div.card .imgbox .cost > svg {
  fill: #a53030;
  height: 1em;
  width: 0.5em;
}
div.card .content {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 1em;
  width: 6.6428571429em;
  height: 40%;
}
div.card .content p {
  margin: 0;
  padding: 0;
}
div.card .content .stat {
  width: 20%;
  flex-basis: 20%;
  flex-grow: 0;
  position: relative;
  width: 1.75em;
  height: 100%;
}
div.card .content .stat svg {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  fill: #524839;
  width: 100%;
  height: fit-content;
}
div.card .content .stat p {
  margin: 0;
  padding: 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: calc(1.75 * var(--card-rem));
  line-height: calc(1.5 * var(--card-rem));
  vertical-align: center;
  font-size: 1em;
  text-shadow: 2px 4px 15px #000, 0px 0px 15px #000;
  color: #a53030;
  font-family: "paint-number", monospace;
}
div.card .content .sigils {
  flex-basis: 60%;
  width: 60%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
div.card .content .sigils .sigil {
  position: relative;
}
div.card .content .sigils .sigil svg {
  display: inline-block;
  height: 100%;
  width: 100%;
}
div.card .content .sigils .sigil .tooltip {
  position: absolute;
  top: 90%;
  left: 10%;
  width: 40ch;
  text-align: left;
  font-size: 1rem;
  z-index: 3;
  padding: 0.5em;
  background-color: #cdba94;
  color: #524839;
  border: 10px solid #73654a;
  display: none;
  border-radius: 10px;
}
div.card .content .sigils .sigil .tooltip .title {
  font-family: "paint-number", serif;
  text-decoration: #a53030 underline;
  text-decoration-thickness: 2px;
  font-weight: 600;
}
div.card .content .sigils .sigil div:hover ~ .tooltip, div.card .content .sigils .sigil .tooltip:hover {
  display: block;
}

